import React, {Component} from "react";
import PropTypes from "prop-types";
import {ARIA, ARIA_LIVE_OPTIONS, styleVariant} from "../../modules/core/utils/ui";
import {OraParentLoader} from "./OraLoader";

class OraMessage extends Component {
    getRole() {
        return this.props.role || (["warn","error"].indexOf(this.props.type) >= 0 ? "alert" : null);
    }

    getLive() {
        if (this.props.live)
            return this.props.live;
        else switch (this.props.type) {
            case "info": return ARIA.POLITE;
            case "warn": return ARIA.ASSERTIVE;
            case "error": return ARIA.RUDE;
            default: return ARIA.ASSERTIVE;
        }
    }

    getClassName() {
        return "opl-msg"
            + styleVariant(" opl-msg--", this.props.variant)
            + styleVariant(" opl-msg--", this.props.type)
            + styleVariant(" ", this.props.className);
    }

    getIconAttr(attr) {
        return "g-icon--" + attr;
    }

    isShow() {
        return this.props.text || this.props.title || React.Children.count(this.props.children) > 0;
    }

    render() {

        return !this.isShow() ? null :
            <div className={this.getClassName()} aria-live={this.getLive()}>

                {
                    this.props.type === "progress" ?
                        <div className="l-row">
                            <div className="l-col l-col-1 u-padding-m-top u-padding-m" ><span>&nbsp;
                                <OraParentLoader  size={"s"} loading={true} />
                            </span>
                            </div>
                            <div className="l-col l-col-11  ">

                                {!this.props.text ? null : <p role={this.getRole()} className={(this.props.icon ? " o-icon-text__text" : " ") + (this.props.fontBold ? " u-font-bold" : "")+"u-padding-top-s"}> {this.props.text}</p>}

                            </div>
                        </div>
                        :

                        (this.props.icon ?
                                <div className={"o-icon-text g-icon g-icon--before " + this.getIconAttr(this.props.iconSize) + " " + this.getIconAttr(this.props.iconType)} >
                                    {!this.props.title ? null : <div className="opl-msg__header">{this.props.title}</div>}
                                    {!this.props.text ? null : <p role={this.getRole()} className={(this.props.icon ? " o-icon-text__text" : " ") + (this.props.fontBold ? " u-font-bold" : "")+("u-margin-left")}>{this.props.text}</p>}
                                    {this.props.children}
                                </div> :
                                <div>
                                    {!this.props.title ? null : <div className="opl-msg__header">{this.props.title}</div>}
                                    {!this.props.text ? null : <p role={this.getRole()} className={(this.props.icon ? " o-icon-text__text" : " ") + (this.props.fontBold ? " u-font-bold" : "")+("u-margin-left")}>{this.props.text}</p>}
                                    {this.props.children}
                                </div>
                        )
                }
            </div>;
    }
}

OraMessage.propTypes = {
    title: PropTypes.string,
    text: PropTypes.string,
    variant: PropTypes.oneOf(["context", "box"]),
    type: PropTypes.oneOf(["info", "warn", "error","progress"]),
    className: PropTypes.string,
    role: PropTypes.string,
    live: PropTypes.oneOf(ARIA_LIVE_OPTIONS),
    icon: PropTypes.bool,
    fontBold: PropTypes.bool,
    iconSize: PropTypes.oneOf(["xxs","xs","xs-s","s","l","xl","xxl"]),
    iconType: PropTypes.string,
    children: PropTypes.any
};

OraMessage.defaultProps = {
    className: "",
    variant: "context",
    type: "info",
    icon: false,
    fontBold: false,
    iconSize: "s",
    iconType: "info"
};

export default OraMessage;